/* eslint-disable */

import React, { useState } from 'react';
import SearchBar from './SearchBar';
import StockInfo from './StockInfo';
import axios from 'axios';
import convert from 'xml-js';
import styled from 'styled-components';

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Title = styled.h1`
  background-color: #0af07a; /*  */
  color: #000000; /*  */
  border-radius: 50px; /*    */
  padding: 10px 20px; /*    */
  font-size: 24px; /*   */
  font-weight: bold; /*   */
  margin-bottom: 20px; /*     */
`;

function App() {
  const [stockData, setStockData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchStockData = async (itmsNm, date) => {
    setLoading(true);
    setError(null);

    const API_KEY = 'CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD+chVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ==';
    const API_URL = `https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo`;

    try {
      const response = await axios.get(API_URL, {
        params: { 
          serviceKey: API_KEY,
          itmsNm: itmsNm,
          basDt: date ? date.replace(/-/g, '') : undefined //    YYYYMMDD  
        }
      });

      console.log('XML :', response.data);

      const jsonResult = convert.xml2js(response.data, { compact: true, spaces: 2 });
      console.log(' JSON:', jsonResult);

      const items = jsonResult.response.body.items.item;

      //       
      const itemArray = Array.isArray(items) ? items : [items];
      const stockItem = itemArray.find(item => item.itmsNm._text.toLowerCase() === itmsNm.toLowerCase());

      if (stockItem) {
        const extractedData = {
          itmsNm: stockItem.itmsNm._text,
          mrktCtg: stockItem.mrktCtg._text,
          clpr: stockItem.clpr._text,
          vs: stockItem.vs._text,
          fltRt: stockItem.fltRt._text,
          basDt: stockItem.basDt._text
        };
        setStockData(extractedData);
      } else {
        setStockData(null);
        setError('     .');
      }
    } catch (error) {
      console.error('API  :', error);
      setError('  .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <AppContainer>
      <div className="App">
        <Title>   </Title>
        <SearchBar onSearch={fetchStockData} />
        {loading && <p> ...</p>}
        {error && <p style={{ color: 'red' }}>{error}</p>}
        {stockData && <StockInfo data={stockData} />}
      </div>
    </AppContainer>
  );
}

export default App;
